import React from 'react';
import Gnb from './Gnb';
import BtnDownloadApp from '../common/buttons/BtnDownloadApp';
import Inner from '../common/layouts/Inner';

const FullHeader = ({ albaRef, handleCloseFullHeader }) => {
  return (
    <div className="fullHeader">
      <Inner>
        <div className="fullHeader__content">
          <div
            className="fullHeader__gnb"
            onClick={(e) => {
              if (
                e.target.tagName === 'A' &&
                e.target.className !== 'alba-link'
              )
                handleCloseFullHeader();
            }}
          >
            <Gnb albaRef={albaRef} />
          </div>
          <div className="fullHeader__downloadApp">
            <p>당근 앱 설치 후 사용해보세요!</p>
            <BtnDownloadApp />
          </div>
        </div>
      </Inner>
    </div>
  );
};

export default FullHeader;
