import { useCallback, useRef } from 'react';
import '../../styles/header.scss';
import FullHeader from './FullHeader';
import FixedHeader from './FixedHeader';
import { Laptop } from '../common/layouts/Responsive';

const Header = ({ globalSearchRef }) => {
  const headerRef = useRef(null);
  const albaRef = useRef(null);
  const handleCloseFullHeader = useCallback(() => {
    headerRef.current.classList.toggle('full');
    albaRef.current.classList.remove('open');
  }, []);
  return (
    <>
      <header className="header" ref={headerRef}>
        <FixedHeader
          globalSearchRef={globalSearchRef}
          handleCloseFullHeader={handleCloseFullHeader}
        />
        <Laptop>
          <FullHeader
            albaRef={albaRef}
            handleCloseFullHeader={handleCloseFullHeader}
          />
        </Laptop>
      </header>
    </>
  );
};

export default Header;
